<template>
  <div class="wrap wrap-goods">
    <!--面包导航-->
    <div class="nav-bar clear-fix">
      <el-breadcrumb separator=">" class="fl-l">
        <el-breadcrumb-item>商品管理</el-breadcrumb-item>
        <el-breadcrumb-item>商品列表</el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <!--搜索-->
    <div class="search report">
      <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="80px">
        <div class="fold">
          <span @click="fold?fold=false:fold=true">查询{{fold?'折叠':'展开'}}
            <i :class="fold?'iconfont icon-fold-open':'iconfont icon-fold-close'"></i>
          </span>
        </div>
        <transition name="el-zoom-in-center">
          <div class="item-list" v-if="fold">   
              <div class="search-top">
                <el-form-item label="价格" class="price">
                  <el-input v-model="searchForm.search_price_min" @blur="validateFloat('search_price_min')" size="small"></el-input> - <el-input v-model="searchForm.search_price_max"  @blur="validateFloat('search_price_max')"></el-input>
                </el-form-item>
                <el-form-item label="重量" class="price">
                  <el-input v-model="searchForm.min_weight" @blur="validateFloat('min_weight')" size="small"></el-input> - <el-input v-model="searchForm.max_weight"  @blur="validateFloat('max_weight')"></el-input>
                </el-form-item>
                <div class="key-word-box">
                  <el-select class="key-word" v-model="searchForm.search_type" slot="prepend" placeholder="选择关键字" clearable size="small">
                      <!-- supplier -->
                      <el-option label="英文名称" value="9" v-if="userType=='supplier'"></el-option>
                      <el-option label="商品编码" value="2" v-if="userType=='supplier'"></el-option>
                      <el-option label="商品货号" value="7" v-if="userType=='supplier'"></el-option>
                      <el-option label="sku编码" value="4" v-if="userType=='supplier'"></el-option>
                      <el-option label="sku货号" value="8" v-if="userType=='supplier'"></el-option> 
                      <!-- admin  -->
                      <el-option label="商家编码" value="6" v-if="userType=='admin'"></el-option>
                      <el-option label="英文名称" value="9" v-if="userType=='admin'"></el-option>
                      <el-option label="商品编码" value="2" v-if="userType=='admin'"></el-option>
                      <el-option label="商品ID" value="3" v-if="userType=='admin'" ></el-option>
                      <el-option label="sku编码" value="4" v-if="userType=='admin'"></el-option>
                      <el-option label="sku ID" value="5" v-if="userType=='admin'"></el-option>
                  </el-select>
                  <el-input type="textarea" v-model="searchForm.keyword" :placeholder="userType=='admin'?'商家编码/商品名称/编码/ID':'商品名称/编码/货号'"></el-input>
                </div>
                  
              </div>
            <el-form-item label="客户经理" v-if="userType=='admin'">
                <el-select v-model="searchForm.goods_manager" size="small" clearable filterable>                              
                    <el-option :value="item.id" :label="item.name" v-for="item in goodsManagersList" :key="item.id"></el-option>
                </el-select>
               
            </el-form-item>              
            <el-form-item label="分类">
              <el-cascader v-model="searchForm.cascader"  :options="cateList" change-on-select @change="cateChange" :props="props" filterable :show-all-levels="false" clearable></el-cascader>
            </el-form-item>
            <el-form-item label="审核状态" v-if="goodsStatus==''">
                <el-select v-model="searchForm.search_audit"  size="small" clearable >
                    <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.auditStatus" :key="item.val"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="商品状态" v-if="userType=='admin'&&goodsStatus==''">
                <el-select v-model="searchForm.is_delete"  size="small" clearable >
                    <el-option value="1" label="正常" ></el-option>
                    <el-option value="2" label="已删除" ></el-option>
                </el-select>
            </el-form-item>
             <el-form-item label="商品品牌">
                <el-select v-model="searchForm.brand_id" size="small" clearable v-if="userType=='supplier'" >       
                    <el-option :value="item.brandId" :label="item.brandName" v-for="item in brandsList" :key="item.brandId"></el-option>
                </el-select>
                <el-autocomplete v-model="searchForm.brandName" :fetch-suggestions="remoteGetBrand"  :trigger-on-focus="false" size="small" :value-key="'brandName'" @select="brandSelect" v-if="userType=='admin'"></el-autocomplete>
            </el-form-item>
            <el-form-item label="物流方式" v-if="userType=='admin'">
                <el-select v-model="searchForm.logis_way" size="small" clearable filterable>                              
                    <el-option :value="item.logisticsCode" :label="item.logisticsName" v-for="item in logisticsList" :key="item.logisticsCode"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="合作平台" v-if="userType=='admin'">
                <el-select v-model="searchForm.cpaCode" size="small" clearable filterable>                              
                    <el-option :value="item.code" :label="item.name" v-for="item in cpaList" :key="item.code"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="运费模板"   v-if="userType=='supplier'">
                <el-select v-model="searchForm.freight_template_id" size="small" clearable filterable >              
                    <el-option :value="item.id" :label="item.templateTitle" v-for="item in freightTemplateList" :key="item.id"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="锁定状态"  v-if="userType=='admin'">
              <el-select v-model="searchForm.is_lock" clearable >
                <el-option label="已锁定" value="1"></el-option>
                <el-option label="未锁定" value="0"></el-option>     
              </el-select>
            </el-form-item>
            <el-form-item label="库存量">
              <el-select v-model="searchForm.is_qyt" clearable >
                <el-option label="有库存" value="1"></el-option>
                <el-option label="无库存" value="0"></el-option>     
              </el-select>
            </el-form-item>
            <el-form-item label="上架状态">
              <el-select v-model="searchForm.is_on_sale" clearable >
                <el-option label="上架" value="1"></el-option>
                <el-option label="下架" value="0"></el-option>     
              </el-select>
            </el-form-item>
            <el-form-item label="商品有效期">
              <el-select v-model="searchForm.sale_closing_date" clearable >
                <el-option label="3天内过期" value="1"></el-option>
                <el-option label="7天内过期" value="2"></el-option>
                <el-option label="30天内过期" value="3"></el-option>
                <el-option label="已过期" value="4"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="发货地"  >
                <el-select v-model="searchForm.shipping_from" size="small" clearable filterable >              
                    <el-option :value="item.code" :label="item.name" v-for="item in shopCountry" :key="item.code"></el-option>
                </el-select>
            </el-form-item>
            <div class="search-btn">
              <el-button type="primary" @click="search_smt">查询</el-button>              
             <goods-export :search="searchForm" :userType="userType" v-if="(userType=='admin'&&authOps.includes('/api/goods/down/process')&&goodsStatus=='')||userType=='supplier'"></goods-export>  
             <!-- <goods-export :search="searchForm" :userType="userType" v-if="(userType=='admin'&&authOps.includes('/api/goods/down/process')&&goodsStatus=='')"></goods-export>   -->
            
            </div>
          </div>
        </transition>
      </el-form>
    </div> 
    <!-- 批量操作 -->
    <div class="doAction" v-if="showBatch">
      <el-button type="primary" @click="doAction('onsale')" size="mini" v-if=" goodsStatus=='' " >上架</el-button>
      <el-button type="primary" @click="doAction('unOnsale')" size="mini" v-if="goodsStatus==''" >下架</el-button>
      <el-button type="primary" @click="doAction('locked')" size="mini" v-if="userType=='admin' && goodsStatus==''" >锁定</el-button>
      <el-button type="primary" @click="doAction('unLocked')" size="mini" v-if="userType=='admin' && goodsStatus==''" >解锁</el-button>
      <el-button type="primary" @click="doAction('adminAudit')" size="mini" v-if="userType=='admin' && goodsStatus=='audit'" >审核</el-button>
      <el-button type="primary" @click="doAction('validityDate')" size="mini" v-if="userType=='supplier'"  >延长有效期</el-button>
      <el-button type="primary" @click="doAction('supplierAudit')" size="mini" v-if="userType=='supplier' &&selectStatus.goodsStatus=='7' " >提交审核</el-button>
      <el-button type="primary" @click="doAction('doCpa')" size="mini" v-if="userType=='admin'&& goodsStatus==''&&authOps.includes('/ec/cpa/goods/add-push')" >推送合作平台</el-button>  
      <el-button type="primary" @click="doAction('delete')" size="mini" v-if="userType=='supplier'" >删除</el-button>  
      <el-button type="primary" @click="doAction('BatchEdit')" size="mini" v-if="userType=='supplier'" >批量修改</el-button>  
    </div>
    <!-- 运营批量审核 -->
    <el-dialog title="商品批量审核" :visible.sync="dialogAdminAudit" width="90%" class="dialogAdminAudit">
      <el-form :model="adminAuditForm" label-width="115px">
        <el-form-item label="审核状态" >
          <el-select v-model="adminAuditForm.auditStatus" clearable size="mini" >
            <el-option label="审核通过" value="2"></el-option>
            <el-option label="审核不通过" value="3"></el-option>
            <el-option label="审核通过并跳过翻译" value="5"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="审核备注" >
          <el-input v-model="adminAuditForm.supplierStatusTxt" type="textarea"></el-input>
        </el-form-item>
        <cpa-push ref="cpaPush-a" :cpadata="cpaList"></cpa-push>
        <el-form-item label="提示:"  class="el-form-item-tip" v-if="adminAuditValidateText">
          <div>审核状态不能为空，审核不通过必须填写备注信息！</div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitAdminAuditCancel">保 存</el-button>
        <el-button type="primary" @click="submitAdminAudit">确 定</el-button>
      </div>
    </el-dialog>
     <!-- 运营  cpa推送 -->
    <el-dialog title="商品批量推送" :visible.sync="dialogCpa" class="dialog-cpa" width="90%">
      <cpa-push ref="cpaPush" :cpadata="cpaList"></cpa-push>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitCpaCancel" size="small">保 存</el-button>
        <el-button type="primary" @click="submitCpa" size="small">确 定</el-button>
      </div>
    </el-dialog>


    <!-- 有效期提示 -->
    <el-dialog title="提示" :visible.sync="dialogValidityDate"   class="dialog-validitydate" top='40%'>
      <div class="txt">您在售的产品中有 <span>{{validityDate.num}}</span>个即将过期的产品，产品过期将自动下架，请尽快处理！</div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogValidityDate = false">取 消</el-button>
        <el-button size="small" type="primary" @click="searchValiditydate">批量处理</el-button>
        <el-button size="small" type="primary" @click="setValiditydateAll">全部处理</el-button>
      </span>
    </el-dialog>
    <!-- 延长有效期 -->
    <el-dialog title="延长有效期" :visible.sync="dialogSetValidityDate"  class="dialog-validitydate" top='20vh'>
      <div class="list"> <el-radio class="radio" v-model="validityDateMonth" label="1">有效期延长30天</el-radio></div>
      <div class="list"> <el-radio class="radio" v-model="validityDateMonth" label="2">有效期延长60天</el-radio></div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogSetValidityDate = false">取 消</el-button>
        <el-button size="small" type="primary" @click="setValiditydate">确定</el-button>
      </span>
    </el-dialog>

<el-dialog title="" :visible.sync="preview.dialogShowAllPic" :modal="false" class="dialog-preview" :top="'40vh'" @close="dialogShowAllPicClose">
  <div class="pic-all">
    <div class="pic-box">
      <div class="pic-title">属性图片</div>
      <div class="pic-list">
        <div class="pic-item" v-for="(item,index) in preview.color_images" :key="index">
            <img :src="item.url" alt="">
            <div>{{item.color}}</div>
        </div>
      </div>
      <div class="pic-title">商品图片</div>
      <div class="pic-list">
        <div class="pic-item" v-for="(item,index) in preview.common_images" :key="index">
            <img :src="item" alt="">   
        </div>
      </div>
    </div>
  </div>
</el-dialog> 
    <!--列表-->
    <el-table :data="goodsList" stripe  v-loading="loading" class="report goods-list" max-height="520" border  @selection-change="handleSelectionChange" style="width:100%;" :fit="false" :tooltip-effect="'light'" >
      <el-table-column type="selection" width="40" fixed="left" :selectable="selectable"></el-table-column>
      <el-table-column label="商品ID" prop="goods_id" align="center" min-width="120" :show-overflow-tooltip="true" v-if="userType=='admin'">
        <template slot-scope="scope">
          <span>{{scope.row.goods_id}}</span>
           <i :class="'iconfont icon-'+(scope.row.is_lock==1?'locked danger':'unlocked primary')" :title="scope.row.is_lock==1?'已锁定':'未锁定'"></i>
        </template>
      </el-table-column>
      <el-table-column label="商品编码" prop="goods_sn" align="center" min-width="120" :show-overflow-tooltip="true"  >
        <template slot-scope="scope">
          <div class="td-goods-sn s"  v-if="userType=='supplier'" @click="doSkip({type:'show',data:scope.row})">
              <span style="color:#0F3FCF;">{{scope.row.goods_sn}}</span>
              <i :class="'iconfont icon-'+(scope.row.is_lock==1?'locked danger':'unlocked primary')" :title="scope.row.is_lock==1?'已锁定':'未锁定'" v-if="userType=='supplier'"></i>
          </div>
          <div class="td-goods-sn s"  v-if="userType=='admin'" @click="doSkip({type:'show',data:scope.row})">
              <span style="color:#0F3FCF;">{{scope.row.goods_sn}}</span>         
          </div>
                
        </template>
      </el-table-column>
      <el-table-column label="商家编码" prop="supplier_code" align="center" min-width="120" :show-overflow-tooltip="true" v-if="userType=='admin'"></el-table-column>
      <el-table-column label="商品名称" prop="goods_name" align="center" width="300" >
        <template slot-scope="scope">
          <div class="td-goods-name clear-fix s"  v-if="userType=='supplier'">
            <div class="img fl-l"><img v-lazy="scope.row.cover_img" alt="" min-width="60" height="60"  ></div>
            <div class="txt">{{scope.row.goods_name}}</div>
          </div>
          <div class="td-goods-name clear-fix" v-if="userType=='admin'&& goodsStatus==''">
            <div class="img fl-l">
              <img v-lazy="scope.row.cover_img" alt="" min-width="60" height="60">              
            </div>
            
            <div class="txt">{{scope.row.goods_name}}</div>
          </div>
          <div class="td-goods-name clear-fix" v-if="userType=='admin'&& goodsStatus=='audit'">
            <div class="img fl-l">
             
              <img v-lazy="scope.row.cover_img" alt="" min-width="60" height="60" @mouseover="showPreview(scope.row)">
              
            </div>
            <div class="txt">{{scope.row.goods_name}}</div>
            
          </div>
        </template>
              

      </el-table-column>         
      <el-table-column label="商品货号" prop="product_code" align="center" min-width="120" :show-overflow-tooltip="true" v-if="userType=='supplier'"></el-table-column> 
      <el-table-column label="商品分类" prop="cat_name" align="center" min-width="120" :show-overflow-tooltip="true"></el-table-column>
      <el-table-column label="品牌名称" prop="brand_name" align="center" min-width="120" :show-overflow-tooltip="true"></el-table-column>
      <el-table-column label="原价|基础售价" prop="market_price" align="left" min-width="120" :show-overflow-tooltip="true" >
        <template slot-scope="scope">
          <div class="td-goods-price">
            <div class="price-market">
              <i class="iconfont icon-yuan"></i>{{scope.row.market_price?parseFloat(scope.row.market_price).toFixed(2):''}}
            </div>
            <div class="price-shop">
              <i class="iconfont icon-shou"></i>{{scope.row.shop_price?parseFloat(scope.row.shop_price).toFixed(2):''}}
            </div>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="SKU售价区间" prop="product_code" align="center" min-width="150" :show-overflow-tooltip="true" >
          <template slot-scope="scope">
            <div @click="showSkuInfo(scope.row.goods_id)" :style="scope.row.max_price>3000?'color:#ea2000;cursor:pointer;':'cursor:pointer;'">{{scope.row.min_price?parseFloat(scope.row.min_price).toFixed(2):""}}~{{scope.row.max_price?parseFloat(scope.row.max_price).toFixed(2):""}}</div>           
          </template>
      </el-table-column>
      <el-table-column label="重量(kg)"  align="center" width="100"  >
           <template slot-scope="scope">
             <div v-if="parseInt(scope.row.goods_weight)>2000" style="color:#ea2000;">{{parseInt(scope.row.goods_weight)/1000}}/kg</div>
             <div v-else>{{parseInt(scope.row.goods_weight)/1000}}/kg</div>
           </template> 
      </el-table-column>  
      <el-table-column label="推送平台" prop="cpa" align="center" width="180" v-if="userType=='admin'" show-overflow-tooltip>
           <template slot-scope="scope">
            <div @click="showCpa(scope.row.goods_id)"> {{scope.row.cpa}} </div>
           </template> 
      </el-table-column>
      <el-table-column label="库存" prop="stock" align="center" min-width="120" :show-overflow-tooltip="true"></el-table-column>
      <el-table-column label="上架状态" prop="is_on_sale" align="center" min-width="120" :show-overflow-tooltip="true">
        <template slot-scope="scope">{{scope.row.is_on_sale==1?'已上架':'未上架'}}</template>
      </el-table-column>
      <el-table-column label="有效期(天)" prop="SCDM" align="center" min-width="160" :show-overflow-tooltip="true" sortable></el-table-column>
      <el-table-column label="运费模板" prop="freight_template_name" align="center" min-width="120" :show-overflow-tooltip="true" v-if="userType=='supplier'"></el-table-column>
    
      <el-table-column label="审核状态" prop="audit_status" align="center" min-width="120" :show-overflow-tooltip="true" class-name="td-goods-status">
        <template slot-scope="scope">
          <span :class="(scope.row.audit_status=='3'||scope.row.audit_status=='6')?'txt warning':'txt'">{{scope.row.audit_status_name}}</span>
          <div class="reason" v-if="(scope.row.audit_status=='3'||scope.row.audit_status=='6')">
            <span :title="scope.row.audit_status_reason"><i class="iconfont icon-warning-out"></i>查看原因</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="商品状态" prop="is_delete" align="center" width="120" :show-overflow-tooltip="true" v-if="userType=='admin'">
          <template slot-scope="scope">
            {{scope.row.is_delete|is_delete}}
          </template>
      </el-table-column>   
      <el-table-column label="操作"  align="left" width="170" fixed="right">
        <template slot-scope="scope"> 
          <!-- 只有下架商品，非审核中，非翻译中的商品可以删除 商品锁定不能删除-->      
          <div class="do-action">
            <span class="action" @click="doSkip({type:'shortcut',data:scope.row})"  v-if="goodsStatus=='' && !(userType=='supplier' && scope.row.is_lock=='1')">免审编辑</span>
            <!-- <span class="action" @click="doSkip({type:'show',data:scope.row})" v-if="userType=='admin'">商品预览</span> -->
            <span class="action" @click="doSkip({type:'edit',data:scope.row})" v-if="userType=='supplier' && scope.row.is_lock=='0' ">编辑</span>
            <span class="action" @click="doSkip({type:'supplierAudit',data:scope.row})" v-if="userType=='supplier' && scope.row.audit_status=='7' && scope.row.is_lock=='0'">提交审核</span>
            
            <span class="action" @click="doSkip({type:'copy',data:scope.row})" v-if="userType=='supplier'" >复制</span>
            
            <span class="action" @click="doSkip({type:'adminAudit',data:scope.row})"  v-if="userType=='admin' && scope.row.audit_status=='1'">审核</span>     
            <span class="action" @click="doSkip({type:'delete',data:scope.row})" v-if="scope.row.is_on_sale=='0' && !(scope.row.audit_status=='1' ||scope.row.audit_status=='2')&& !(userType=='supplier' && scope.row.is_lock=='1')">删除</span>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1"> 
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="searchForm.pageNum" :page-sizes="pageSizeArr" :page-size="searchForm.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>
    <!-- cpainfo -->
    <el-dialog title="详情" :visible.sync="dialogCpaInfo">
      <el-table :data="cpaInfo" border stripe max-height="400">
        <el-table-column prop="cpaName" label="推送平台" ></el-table-column>
        <el-table-column property="areaName" label="关联区域/国家" ></el-table-column>
        <el-table-column property="logisticsName" label="物流方式"></el-table-column>
        <el-table-column  label="运费(USD)">
          <template slot-scope="scope">{{(scope.row.freight/100).toFixed(2)}}</template>
        </el-table-column>
      </el-table>
    </el-dialog>
    <!-- sku 价格info -->
    <el-dialog title="sku详情" :visible.sync="dialogSkuInfo" class="dialog-skuinfo">
      <el-table :data="skuInfoList" border stripe max-height="400">
        <el-table-column prop="product_sn" label="sku编码" width="150"></el-table-column>
        <el-table-column property="product_title" label="sku货号" width="150"></el-table-column> 
        <el-table-column  label="属性" class="attrs" >
          <template slot-scope="scope">
            <div class="item" v-for="(item,index) in scope.row.price_attrs" :key="index">
              {{item.attr_name}}:{{item.attr_value}}
            </div>
          </template>
        </el-table-column>
        <el-table-column property="product_price" label="价格" width="100"></el-table-column>
        <el-table-column property="product_number" label="库存" width="100"></el-table-column>        
      </el-table>
    </el-dialog>

  </div>
</template>

<script>
import format from "date-format";
import { mapGetters } from 'vuex';
const GoodsExport = r => require.ensure([], () => r(require('@/components/sys/goods/goods-export.vue')), 'goods-other');
const CpaPush = r => require.ensure([], () => r(require('@/components/sys/goods/cpa-push.vue')), 'goods-other');
export default {
  components:{
    GoodsExport,
    CpaPush,
  },
  data() {
    return {
      //搜索折叠
      fold: true,
      searchForm: {
        brandName: "",
        brand_id: "",
        search_price_min: "",
        search_price_max: "",
        search_type: "",
        keyword: "",
        cat_id: "",
        is_qyt: "",
        is_lock: "",
        is_on_sale: "",
        logis_way: "",
        freight_template_id: "",
        search_audit: "",
        sale_closing_date: "",
        cpaCode:'',
        shipping_from:'',
        pageNum: 1,
        pageSize: 20,
        is_delete:'1',
        min_weight:'',
        max_weight:'',
        cascader:[]
      },
      showBatch:true,
      //用户类型
      userType: "",
      //商品状态
      goodsStatus: "",
      //报表列表
      loading: false,
      total: 0,
      goodsList: [],
      goodsAllIds: [],

      //商品分类列表
      cateList: [],
      cpaList: [],
      cpaSortList:[],
      cpaListAll:[],
      brandsList: [],
      //物流方式
      logisticsList: [],
      freightTemplateList: [],
      props: {
        value: "catId",
        label: "catName"
      },
      //品牌
      brandProps: {
        value: "brandName",
        label: "brandName"
      },
      // 复选数据
      selectData: [],
      actionType: "",
      goodsIds: [],
      goodsSns: [],
      //批量审核表单
      adminAuditForm: {
        supplierStatusTxt: "",
        auditStatus: "",
        cpaList: []
      },
      cpaForm:{
       cpaList: [],
       cpaSearch:''
      },
      isIndeterminate:false,
      cpaCheckAll:false,
      dialogAdminAudit: false,
      dialogLogistic:false,
      dialogValidityDate:false,
      dialogSetValidityDate:false,
      dialogSetValidityDateType:0,//过期商品是否是全部处理还是批量处理
      dialogCpa:false,
      adminAuditValidateText: false,
      LogisticValidateText: false,
      cpaValidateText: false,
      //商品操作
      skipType: {},
      //批量操作按钮显示
      selectStatus:{
        goodsStatus:"",
        goodsLogistic:''
      },
      logisticForm:{
        price:'',
        logisticsTypeId:'',
        goodsIdRows:[]
      },
      validityDate:{
        num:''
      },
      //延长有效期月份
      validityDateMonth:'2',
      //pagesize
      pageSizeArr:[20,50, 100],
      //CPA info
      cpaInfo:[],
      dialogCpaInfo:false,
      areas:[],
      //商品发货地
      shopCountry:[],
      preview:{
        dialogShowAllPic:false,
        color_images:[],
        common_images:[]
      },
      //skuinfo
      skuInfoList:[],
      dialogSkuInfo:false,
      goodsManagersList:[],//客户经理列表       
    };
  },
  computed: {
    ...mapGetters({
        authSelect: 'getAuthSelect',
        authOps: 'getAuthOps'
    })
  },
  filters: {
    dateFormat(date) {
      return date ? format("yyyy-MM-dd hh:mm", new Date(date)) : "";
    },
    is_delete(val){
      let str="";
      switch(val){
        case '0':
          str='正常'
        break;
        case '1':
          str='已删除'
        break;
        case '2':
          str='已删除'
        break;
      }
      return str;
    }
  },
  methods: {
    showSkuInfo(goods_id){
      this.axios.post("/proxy/fv/"+this.userType+"/goods.php?act=getGoodsSkuList",{body:{goods_id:goods_id}}).then(res=>{
        let data=res.data;
        if(data.head.code=="10000"){
          this.dialogSkuInfo=true;
          this.skuInfoList=data.body.rows;
          console.log(this.skuInfoList)
        }
      })
    },
    // 初始化搜索条件
    initSearchForm(){
      this.searchForm={
        brandName: "",
        brand_id: "",
        search_price_min: "",
        search_price_max: "",
        search_type: "",
        keyword: "",
        cat_id: "",
        is_qyt: "",
        is_lock: "",
        is_on_sale: "",
        logis_way: "",
        freight_template_id: "",
        search_audit: "",
        sale_closing_date: "",
        cpaCode:'',
        shipping_from:'',
        pageNum: 1,
        pageSize: 20,
        is_delete:'1',
        min_weight:'',
        max_weight:'',
        goods_manager:'',
        cascader:[]
        
      }
    },
    //重新渲染商品状态
    renderGoodsStatus(type){
      for(let i in this.myConfig.status.auditStatus){
        if(type==this.myConfig.status.auditStatus[i].val){
          return this.myConfig.status.auditStatus[i].label;
        }
      }
      return ''
    },
    //验证两位小数
    validateFloat(data){
      switch(data){
        case 'search_price_min':
          this.searchForm.search_price_min = isNaN(parseFloat(this.searchForm.search_price_min))?'':parseFloat(this.searchForm.search_price_min).toFixed(2)
          break;
        case 'search_price_max':
          this.searchForm.search_price_max = isNaN(parseFloat(this.searchForm.search_price_max))?'':parseFloat(this.searchForm.search_price_max).toFixed(2)
          break;
        case 'logisticPrice':
          this.logisticForm.price = isNaN(parseFloat( this.logisticForm.price))?'':parseFloat( this.logisticForm.price).toFixed(2)
          break;
      }
     
    },
    search_smt() {
      this.searchForm.pageNum = 1;
      //去掉前后空格
      for (let i in this.searchForm) {
        if (typeof this.searchForm[i] == "string") {
          this.searchForm[i] = this.searchForm[i].trim();
        }
      }
      this.getGoodsList();
      this.showBatch=this.searchForm.is_delete=='2'?false:true;
     
    },

    //商品列表获取
    DateToString() {
      for (let i in this.searchForm) {
        //日期转换
        if (this.searchForm[i] instanceof Date) {
          this.searchForm[i] = format(
            "yyyy-MM-dd",
            new Date(this.searchForm[i])
          );
        }
        //去掉前后空格
        if (this.searchForm[i] instanceof String) {
          this.searchForm[i] = this.searchForm[i].trim();
        }
      }
    },
    getGoodsList() {
      this.loading = true;
      this.DateToString();
      if(this.goodsStatus =="audit"){
          this.searchForm.sort_order="ASC";
      }
      this.axios.post("/proxy/fv/" + this.userType + "/goods.php?act=getGoodsList", {body: this.searchForm}).then(response => {
        this.loading = false;
        let data = response.data;
        if (data.head.code == "10000") {
          if(data.body.sub_code=='20000'){
            this.$message({
              message:data.body.sub_msg,
              type: "error"
            });
          }else{
            this.selectStatus.goodsStatus=this.searchForm.search_audit;
            // this.selectStatus.goodsLogistic=this.searchForm.freight_template_id;         
            this.goodsList = data.body.goods;
            this.total = parseInt(data.body.filter.record_count);
            this.goodsAllIds = this.goodsList.map(item => {
              return item.goods_id;
            });
            if(this.goodsAllIds.length>0){
              this.getProductStock();
              if (this.userType == "admin") {
                this.getProductCpa();
              }
            }
          }
          
        }
      });
      
    },
    //获取商品库存
    getProductStock() {
      this.axios
        .post("/proxy/goods/api/products/stockCount", {
          body: { goodsIds: this.goodsAllIds }
        })
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            let list = data.body.sku;
            for (let i in list) {
              for (let j in this.goodsList) {
                if (list[i].goodsId == this.goodsList[j].goods_id) {
                  this.goodsList[j].stock = list[i].productNumber;
                  break;
                }
              }
            }
            this.goodsList = JSON.parse(
              JSON.stringify({ tmp: this.goodsList })
            ).tmp;
          }
        });
    },
    //获取商品cpa列表
    getProductCpa() {
      this.axios.post("/proxy/ec/cpa/goods/in", {body: { goodsIds: this.goodsAllIds.join() }}).then(response => {
        let data = response.data;
        if (data.head.code == "10000") {
          let list = data.body.map;
          for (let i in list) {
            for (let j in this.goodsList) {
              if (i == this.goodsList[j].goods_id) {
                this.goodsList[j].cpa = list[i];
                break;
              }
            }
          }
          this.goodsList = JSON.parse(
            JSON.stringify({ tmp: this.goodsList })
          ).tmp;
        }
      });
    },

    //分页
    handleCurrentChange(val) {
      this.searchForm.pageNum = val;
      this.getGoodsList();
    },
    handleSizeChange(val) {
      this.searchForm.pageSize = val;
      this.getGoodsList();
    },

    //获取商品分类列表
    getCateList() {
      this.axios.post("/proxy/report/api/common/category/options",{ body: {} }).then(response => {
        let data = response.data;
        if (data.head.code == "10000") {
          this.cateList = data.body;
        }
      });
    },
    //获取所有的商品经理
    getGoodsManagers(){
      this.axios.post( "/proxy/fv/admin/goods.php?act=getGoodsManagers",{ body: {} }).then(response => {
        let data = response.data;
        if (data.head.code == "10000") {
          this.goodsManagersList = data.body.rows;
        }
      });
    },

    //选择分类
    cateChange(cateArr) {
      this.searchForm.cat_id = cateArr[cateArr.length - 1];
    },

    // 获取运费模板方式
    getFreightTemplate() {
      this.axios.post("/proxy/freight/api/template/list", { body: { pager:{
          page: 1,
          pageSize: 1000
      }} }).then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            //去除默认运费模板 
            this.freightTemplateList =data.body.rows;
          }
        });
    },
    // 获取物流方式
    getLogistics() {
      this.axios.post(this.myConfig.code.proxy + "/logistics/logistics/queryType", { body: { languageCode: "cn", status: 0 } }).then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.logisticsList = data.body.logisticsTypes;
          }
        });
    },
    formatterAudit(row, a, val) {
      for (let i in this.myConfig.status.auditStatus) {
        if (this.myConfig.status.auditStatus[i].val == val) {
          return this.myConfig.status.auditStatus[i].label;
          break;
        }
      }
    },

    //获取品牌
    getBrandList() {
      if (this.userType == "admin") {
        this.axios.post(this.myConfig.code.proxy + "/goods/api/brand/list", { body: {} }).then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.brandsList = data.body.rows;
          }
        });
      } else {
        this.axios.post("/proxy/fv/" + this.userType + "/goods.php?act=getSupplierBrand",{}).then(res => {
          let data = res.data;
          this.brandsList = data.body.rows;
        });
      }
    },
    //品牌搜索
    remoteGetBrand(queryString, cb) {
      let its = this.brandsList;
      var results = queryString? its.filter(this.createFilter(queryString)): [];
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.brandName
            .toLowerCase()
            .indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    brandSelect(item) {
      this.searchForm.brand_id = item.brandId;
    },

    /******      批量操作   ************/
    //复选框
    handleSelectionChange(val) {
      this.selectData = val;
    },
    doAction(type) {
      this.actionType = type;
      // 获取ids
      this.goodsIds = [];
      this.goodsSns = [];
      for (let i in this.selectData) {
        this.goodsIds.push(this.selectData[i].goods_id);
        this.goodsSns.push(this.selectData[i].goods_sn);
      }
      if (this.goodsIds.length == 0) {
        this.$message({
          message: "请选择商品",
          type: "error"
        });
        return false;
      }
      switch (this.actionType) {
        case "onsale":
          this.doOnsale();
          break;
        case "unOnsale":
          this.doOnsale();
          break;
        case "locked":
          this.doLocked();
          break;
        case "unLocked":
          this.doLocked();
          break;
        case "adminAudit":
          this.adminAudit();
          break;
        case "supplierAudit":
          this.supplierAudit('batch');
          break;
        case "logistic":
          this.logisticForm={
            price:'',
            logisticsTypeId:'',
            goodsIdRows:[]
          }
          this.dialogLogistic=true;  
          break;
        case "validityDate"://分批处理过期商品
            this.dialogSetValidityDate=true;
            this.dialogSetValidityDateType=0;
            this.validityDateMonth='2';
          break;
        case "delete":
            this.goodsDelete('batch');
          break;
        case "doCpa":   
          
          this.dialogCpa=true;
          if(this.$refs.cpaPush){
            this.$refs.cpaPush.init();
          }
          break;
          //批量修改物流
        case "BatchEdit":
            this.$store.dispatch('setGoodsBatch',this.goodsIds);
            sessionStorage.setItem("batch",JSON.stringify({gids:this.goodsIds}));
            window.open('/goods/batch-edit');
        break;  
      }
    },
   
    //推送cpa
    submitCpa(){
      let data=this.$refs.cpaPush.cpaForm;
      if(data.cpaList.length==0){
        this.$message({
          message: '请选择合作平台',
          type: 'warning'
        });
        return false;
      }
      let tmpArr = [];
      for (let i in this.goodsIds) {
        tmpArr.push({
          goodsId: this.goodsIds[i],
          goodsSn: this.goodsSns[i]
        });
      }
      this.axios.post("/proxy/ec/cpa/goods/add", {body: {
        cpaCode: data.cpaList.join(","),
        list: tmpArr}
        }).then(res => {
          let data = res.data;
          this.$message({
            message:data.head.code == "10000" ? "操作成功" : data.body.sub_msg,
            type: data.head.code == "10000" ? "success" : "error",
            duration:3000
          });   
          this.dialogCpa = false;
          this.getGoodsList();    
        });
           
    },
    submitCpaCancel(){
      this.$refs['cpaPush'].cpaForm.cpaSearch="";
      this.$refs['cpaPush'].doCpaSearch();
    },
    //过期商品分批处理
    searchValiditydate(){
      this.dialogValidityDate=false;
      this.searchForm.is_on_sale='1';
      this.searchForm.sale_closing_date='2';
      this.getGoodsList();
    },
    //过期商品全部处理
    setValiditydateAll(){
      this.dialogValidityDate=false;
      this.dialogSetValidityDate=true;
      this.dialogSetValidityDateType=1;
      this.validityDateMonth='2';
    },
    //批量设置有效期
    setValiditydate(){
        
        let params={};
        params.type=this.validityDateMonth;
        params.setAll=this.dialogSetValidityDateType;
        if(!params.setAll){
          params.gids=this.goodsIds.join();
        }
        this.axios.post('/proxy/fv/'+this.userType+'/goods.php?act=batchSetTimeGoods',{body:params}).then(res=>{
          let data=res.data;
          if(data.head.code=='10000'){
            this.$message({
              message: data.head.code == "10000" ? "操作成功" : data.body.sub_msg,
              type: data.head.code == "10000" ? "success" : "error"
            });
            if (data.head.code == "10000") {
              this.dialogSetValidityDate=false;
              this.getGoodsList();
            }
          }
        })
    },
    //批量锁定或者解锁
    doLocked() {
      this.axios.post("/proxy/fv/admin/goods.php?act=batchlock", { body: {gids: this.goodsIds.join(","),is_lock: this.actionType == "locked" ? 1 : 0 } }).then(res => {
          let data = res.data;
          this.$message({
            message: data.body.sub_msg,
            type: data.head.code == "10000" ? "success" : "error"
          });
          if (data.head.code == "10000") {
            this.getGoodsList();
          }
        });
    },
    // 批量上下架
    doOnsale() {
      this.axios
        .post("/proxy/goods/api/syn/sale", {
          body: {
            goodsIds: this.goodsIds,
            isOnSale: this.actionType == "onsale" ? true : false
          }
        })
        .then(res => {
          let data = res.data;
          this.$message({
            message: data.head.code == "10000" ? "操作成功" : data.body.sub_msg,
            type: data.head.code == "10000" ? "success" : "error"
          });
          if (data.head.code == "10000") {
            this.getGoodsList();
          }
        });
    },
    // 运营商商品批量审核
    adminAudit() {
      this.adminAuditForm = {
        supplierStatusTxt: "",
        auditStatus: "",
        cpaList: []
      };
      if(this.$refs['cpaPush-a']){
        this.$refs['cpaPush-a'].init();
      }
      this.isIndeterminate=false;
      this.cpaCheckAll=false;
      this.dialogAdminAudit = true;
      this.getCpaList();
    },
    //获取cpa列表
    getCpaList() {
      let body = {};
      this.axios.post("/proxy/ec/cpa/partners/select", { body: { active: 1 } }).then(res => {
        let data = res.data;
        if (data.head.code == "10000") {
          this.cpaList = data.body.list;
          this.cpaListAll=[];
          for(let item of [...this.cpaList].values()){
            this.cpaListAll.push(item.code)
          }
        }
      });
    },
    //条件运营商品审核表单
    submitAdminAudit() {
      if (!this.adminAuditForm.auditStatus||(this.adminAuditForm.auditStatus == "3" && this.adminAuditForm.supplierStatusTxt.trim() == "")) {
        this.$message({
          type:'error',
          message:'审核状态不能为空，审核不通过必须填写备注信息！'
        })
        return false;
      } else {
        this.adminAuditForm.goodsIds = this.goodsIds;
        this.adminAuditForm.userAccount = JSON.parse( window.localStorage.getItem("userInfo")).userAccount;
        //推送至翻译
        this.axios.post("/proxy/goods/api/goods/audit", {body: this.adminAuditForm}).then(res => {
            let data = res.data;
            let tmpArr = [];
            for (let i in this.goodsIds) {
              tmpArr.push({
                goodsId: this.goodsIds[i],
                goodsSn: this.goodsSns[i]
              });
            }
            if (data.head.code == "10000") {     
              let data=this.$refs['cpaPush-a'].cpaForm;       
              //推送cpa
              if(data.cpaList.length>0){
                this.axios.post("/proxy/ec/cpa/goods/add", {
                  body: {
                    cpaCode: data.cpaList.join(","),
                    list: tmpArr
                  }
                }).then(res => {
                  let data = res.data;
                  this.$message({
                    message:data.head.code == "10000" ? "操作成功" : data.body.sub_msg,
                    type: data.head.code == "10000" ? "success" : "error"
                  });
                  
                });
              }  
              
            }
            this.dialogAdminAudit = false;
            this.getGoodsList();
          });
      }
    },
    //cpa搜索取消
    submitAdminAuditCancel(){
       this.$refs['cpaPush-a'].cpaForm.cpaSearch="";
       this.$refs['cpaPush-a'].doCpaSearch();
    },

    //快捷操作
    doSkip(command) {
      this.skipType = command;
      switch (this.skipType.type) {
        case "shortcut": //快捷编辑
          window.open("/vendor/goods-shortcut/" +(this.userType=='admin'?'a':'s') +"/" +this.skipType.data.goods_id);          
          break;
        case "show": //查看
          window.open("/proxy/fv/" +this.userType + "/goods.php?act=product_audit&gid=" + this.skipType.data.goods_id + "&type="+this.userType);
          break;
        case "adminAudit":              
          window.open("/proxy/fv/" +this.userType +"/goods.php?act=product_audit&gid=" + this.skipType.data.goods_id +"&type=" +this.userType);     
          break;
        case "supplierAudit":       
          this.supplierAudit('single')
         break;
         //编辑商品
        case "edit":
          this.$router.push('/vendor/goods-add/edit?id='+this.skipType.data.goods_id+'&cate='+this.skipType.data.cat_id+"&status=" + this.skipType.data.audit_status)                 
         break;
         //复制商品
        case "copy": 
          this.$confirm('您确定要复制该商品吗？', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'success',
            callback:(action)=>{            
                if(action=='confirm'){
                  this.axios.post('/proxy/fv/supplier/goods.php?act=checkHasErrorImage',{body:{goods_id:this.skipType.data.goods_id}}).then(res=>{
                    let data=res.data;
                    if(data.head.code=='10000'){
                      this.$router.push('/vendor/goods-add/copy?id='+this.skipType.data.goods_id+'&cate='+this.skipType.data.cat_id);
                    }
                  })
                          
                }else{
                  this.$message({
                    type: 'info',
                    message: '已取消复制'
                  });  
                }
            }    
          });

          return;      
          localStorage.setItem("hrf",JSON.stringify({
              uri:"/fv/" +this.userType +"/goods.php?act=edit&goods_id=" + this.skipType.data.goods_id +"&cat_id="+this.skipType.data.cat_id+"&status=" + this.skipType.data.audit_status,time: new Date().getTime()
            })
          );
          window.location.href="/proxy/fv/" +this.userType +"/goods.php?act=edit&goods_id=" + this.skipType.data.goods_id +"&cat_id="+this.skipType.data.cat_id+"&status=" + this.skipType.data.audit_status;
         break;
         case "delete":       
          this.goodsDelete('single')
         break;
      }
    },
    //商品删除
    goodsDelete(type){
      let msg='';
      if(type=='single'){
        msg='此操作将删除该商品, 是否继续?';
      }else{
        msg='上架、审核中和翻译中的商品不能被删除,将在所选删除列表中自动过滤！';
      }    
      this.$confirm(msg, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let tmpArr=[];
          if(type =='single'){
            tmpArr.push(this.skipType.data.goods_id);
          }else{
            tmpArr=this.goodsIds.concat();
          }
           this.axios.post('/proxy/goods/api/goods/delete/logic',{body:{goodsIds:tmpArr,isDelete:1}}).then(res=>{
            let data = res.data;
            this.$message({
              message:
                data.head.code == "10000" ? "操作成功" : data.body.sub_msg,
              type: data.head.code == "10000" ? "success" : "error"
            });
            if(data.head.code =='10000'){
              this.getGoodsList();
            }
          })

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    },
    //商品推送翻译
    supplierAudit(type){
      let tmpArr=[];
      if(type =='single'){
        tmpArr.push(this.skipType.data.goods_id);
      }else{
        tmpArr=this.goodsIds.concat();
      }
      this.axios.post('/proxy/goods/api/goods/audit',{body:{goodsIds:tmpArr,auditStatus:1,userAccount:JSON.parse( window.localStorage.getItem("userInfo")).userAccount}}).then(res=>{
        let data = res.data;
        this.$message({
          message:
            data.head.code == "10000" ? "操作成功" : data.body.sub_msg,
          type: data.head.code == "10000" ? "success" : "error"
        });
        if(data.head.code =='10000'){
          if(data.body.auditErrorGoodsSns.length>0){
            let _html=`<div class="content">
                <div class="title" style="font-weight:700;color:#222;">以下编码的商品图片有错误，请修改后再提交审核</div>
                <div class="list" style="word-break: break-all;">商品编码：${data.body.auditErrorGoodsSns.join(';')}</div>
            </div>`;
            this.$notify({
              type:'error',
              title: '警告',
              dangerouslyUseHTMLString: true,
              customClass:'notice-audit-goods',
              message: _html,
              duration: 0,
              offset:400
            });
          }
          this.getGoodsList();
        }
      })

    },
    
    //查询商品有效期
    getGoodsValidityDate() {
      this.axios.post("/proxy/fv/supplier/goods.php?act=getOutTimeGoods", {}).then(res => {
        let data=res.data;
        if(data.body.number){
          this.validityDate.num=data.body.number;
          this.dialogValidityDate=true;
        }
      });
    },
    
    //商品是否可以勾选
    selectable(row) {
      if (row.is_lock == "1" && this.userType == "supplier") {
        return false;
      } else {
        return true;
      }
    },
    //获取区域列表
    getAreas(){
      this.axios.post('/proxy/freight/api/area/list',{body:{}}).then(res=>{
        let data=res.data;
        if(data.head.code=='10000'){
          this.areas=data.body.areas;
        }
      })
    },
    // 查看cpa信息
    showCpa(id){
      this.getCpaInfo(id);
    },
    getCpaInfo(id){
        this.axios.post("/proxy/ec/cpa/goods/in", {body: { goodsIds: id }}).then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            let cpaCodeArr=data.body.goodsCpaCode[id];
            this.axios.post('/proxy/goods/api/goods/detail',{body:{goodsId:id}}).then(res=>{
              let data=res.data;
              if(data.head.code=='10000'){
                let info=Object.values(data.body.freightCpaPriceVoMap);
                let tmpArr=[];
                for(let i in info){
                  if(cpaCodeArr.includes(info[i].cpaCode)){
                      tmpArr.push(info[i])
                  }
                }
                info=tmpArr;
                for(let i in info){
                  let item=info[i];
                  for(let j in this.cpaList){  
                    if(item.cpaCode==this.cpaList[j].code){
                        item.cpaName=this.cpaList[j].name;
                        break;
                    }
                  }
                  for(let j in this.logisticsList){  
                    if(item.logisticsTypeCode==this.logisticsList[j].logisticsCode){
                        item.logisticsName=this.logisticsList[j].logisticsName;
                        break;
                    }
                  }
                  for(let j in this.areas){  
                    for(let k in this.areas[j].children){
                      if(item.freightAreaId==this.areas[j].children[k].id){
                        item.areaName=this.areas[j].children[k].areaName;
                        break;
                      }   
                    }
                  }
                }
              
                this.cpaInfo=info;
                this.dialogCpaInfo=true;
              }
            })
 
          }
        })  
    },
    //商品发货地国家
    getShopCountry(){
      this.axios.post("/proxy/fv/"+ this.userType+"/goods.php?act=getShippingCountry", {}).then(response => {
        let data = response.data;
        if (data.head.code == "10000") {
            this.shopCountry=data.body.shipping_countries;
        }
      })      
    },
    //显示商品图片预览
    showPreview(row){
      console.log(row);
      this.preview.color_images=row.color_images;
      this.preview.common_images=row.common_images;
      this.preview.dialogShowAllPic=true;
    },
    dialogShowAllPicClose(){
      this.preview.dialogShowAllPic=false;
      this.preview={
        dialogShowAllPic:false,
        color_images:[],
        common_images:[]
      };

    }
    
  },

  created() {
    this.initSearchForm();

    
    if (this.$route.name == "vendor-goods-list-audit") {
      this.searchForm.search_audit = 1;
      this.goodsStatus = "audit";
    } else {
      this.searchForm.search_audit = "";
      this.goodsStatus = "";
    }
    if (JSON.parse(window.localStorage.getItem("userInfo")).institutionTypeId === "SYSTEM_OPERATOR") {
      this.userType = "admin";
      this.pageSizeArr=[20,30,50,100,200,300,400,500];
      this.getGoodsManagers();
    } else {
      this.userType = "supplier";
      this.pageSizeArr=[20,30,50,100];
      
      let notice=this.$route.query.notice;
      if(notice=='allTerm'){
        this.dialogSetValidityDate=true;
        this.dialogSetValidityDateType=1;
        this.validityDateMonth='2';
        localStorage.setItem('hrf', JSON.stringify({ uri:"/goods/list", time: new Date().getTime() }))
      }else if(notice=='batchTerm'){
        this.searchForm.is_on_sale='1';
        this.searchForm.sale_closing_date='2';
        this.getGoodsList();
        localStorage.setItem('hrf', JSON.stringify({ uri:"/goods/list?", time: new Date().getTime() }))
      }
    }
    // 处理请求参数
    console.log(this.$route);
    for(let i in this.$route.query){
      if(this.$route.query[i]!==''){
         this.searchForm[i]= this.$route.query[i];
         if(i=='search_audit'){
           this.searchForm[i]= parseInt(this.$route.query[i]);
         }
      }
    }
    // if(this.$route.query.is_on_sale) this.searchForm.is_on_sale=this.$route.query.is_on_sale;
    
    this.getLogistics();
    this.getFreightTemplate();
    this.getBrandList();
    this.getCateList();
    this.getGoodsList();
    this.getCpaList();
    this.getAreas();
    this.getShopCountry();
    
    
  },
  watch: {
    $route() {
      this.initSearchForm();   
      if (this.$route.name == "vendor-goods-list-audit") {
        this.searchForm.search_audit = 1;
        this.goodsStatus = "audit";
      } else {
        this.searchForm.search_audit = "";
        this.goodsStatus = "";
      }
      if (JSON.parse(window.localStorage.getItem("userInfo")).institutionTypeId ==="SYSTEM_OPERATOR" ) {
        this.userType = "admin";
        this.pageSizeArr=[20,30,50,100,200,300,400,500];
        this.getGoodsManagers();
      } else {
        this.userType = "supplier";
        this.pageSizeArr=[20,30,50,100];       
      }
      this.getLogistics();
      this.getFreightTemplate();
      this.getBrandList();
      this.getCateList();
      this.getGoodsList();
      this.getCpaList();
      this.getAreas();
      this.getShopCountry();
      
    }
  }
};
</script>


<style lang="less" scoped>
.wrap {
  .el-checkbox{
    width: 200px;
    margin-left: 0!important;
    line-height: normal;
    .el-checkbox__label{
      width: 150px;
          word-break: break-word;
    white-space: normal;

    }
  }
  padding-right: 20px;
  .goods-list {
    .td-goods-sn.s{
      cursor: pointer;
    }
    .td-goods-name {     
      text-align: left;
      cursor: pointer;
      .img {
        border: 1px solid #666;
        border-radius: 4px;
        padding: 3px;
        height: 66px;
        background: #fff;
        img {
          border-radius: 2px;
        }
        position: relative;
        .show-all-pic{
          position: absolute;
          left: 0;
          top:0;
          width: 0;
        }
        &:hover{
          .show-all-pic{
            width: auto;
          }
        }
      }
      .txt {
        margin-left: 80px;
      }
    }
    .td-goods-price {
      .iconfont {
        font-size: 23px;
        display: inline-block;
        vertical-align: middle;
      }
      .price-shop {
        margin-top: 4px;
        .iconfont {
          color: #3da8e5;
        }
      }
    }
    .td-goods-status{
      .warning{
        color:#ff4949;

      }
      .reason{
       
        cursor: pointer;
      }
      .iconfont{
        font-size: 12px !important;
        margin-right: 6px;
      }
    }
  }
  .pagination {
    margin-top: 15px;
    text-align: right;
  }
  .search {
    .search-top {
     
      .el-input__inner {
        height: 28px;
      }
    }
  }
  .doAction {
    padding-bottom: 10px;
    padding-left: 55px;
  }
  .do-action{
    width: 100%;
    .action{
      display: inline-block;
      width: 60px;
      text-align: center;
      color: #0F3FCF;
      cursor: pointer;
      margin-left:10px;
      text-align: left
    }
  }
}
</style>
<style lang="less">
.search-btn{
    text-align:right;
    padding-bottom:10px;
    padding-right:20px;
}
.search {
  .search-top {
    .el-input {
      // height: 30px;
    }
    .el-textarea {
      width: 190px;
      vertical-align: top;
      margin-left:-1px;
      
    }

    .key-word-box{
      display:inline-block;
      padding-top:6px;
    }
    .key-word {
      .el-input__inner {
        height: 28px;
        border-radius: 4px 0 0 4px;
        width: 110px;
        line-height:28px;
      }
    }

    .el-textarea__inner {
      min-height: 20px !important;
      height: 28px;
      margin-left: -3px;
      border-radius: 0px 4px 4px 0;
      padding:2px 5px;
    }
    .price {
     
      .el-input {
        display: inline-block;
        width: 106px;
        // line-height: 28px;
        vertical-align: top;
      }
    }
  }
}
.el-dropdown-goods-list {
  margin-left: -133px;
}
.el-form-item-tip {
  color: #ff4949;
}
.el-dropdown-goods-list {
  li {
    font-size: 14px;
    line-height: 22px;
  }
}
.dialogLogistic{
  .tip{
    line-height:normal;
    
  }
  .el-input, .el-input__inner{
    width:auto;
  }
  .el-input__inner{
      width:150px;
      display:line-block;
      height: 26px;
      font-size:12px;
    }
  .el-form-item{
